.c-post{
    justify-self: stretch;
    align-self:   stretch;

    /*height: 30%;*/
    width:  80%;

    display: grid;
    grid-template-rows:     minmax(auto, 70%)
                            minmax(auto, 30%);
    grid-template-columns:  minmax(auto, 10%)
                            minmax(auto, 90%);

    /*border: 1px solid #ffc34942;*/
    /*border-radius: 2rem;*/
    /*background: linear-gradient(to top, #fefcea,#b3ff6657);*/
    border-bottom: 0.05rem solid #0fc19e;

    margin: auto auto 1rem;
}
